import { setTextContent } from './setTextContent'

const STYLE = 'style'
const CHILDREN = 'children'

const setInitialDOMProperties = (
  tag: string,
  domElement: Element,
  nextProps: Record<string | number, any>
) => {
  for (const propKey in nextProps) {
    if (!nextProps.hasOwnProperty(propKey)) continue

    const nextProp = nextProps[propKey]

    if (propKey === STYLE) {
    } else if (propKey === CHILDREN) {
      if (typeof nextProp === 'string') {
        const canSetTextContent = tag !== 'textarea' || nextProp !== ''

        if (canSetTextContent) {
          setTextContent(domElement, nextProp)
        }
      } else if (typeof nextProp === 'number') {
        setTextContent(domElement, nextProp + '')
      }
    } else if (nextProp !== null) {
      //todo
    }
  }
}

/**
 * åå§ådomå±æ§
 * @param domElement domåç´ 
 * @param tag domçtagå¯¹åºReact.createElementçç¬¬ä¸ä¸ªåæ°
 * @param rawProps å¯¹åºäºReact.createElementçç¬¬äºä¸ªåæ°ï¼åå«childrenï¼
 */
export const setInitialProperties = (
  domElement: Element,
  tag: string,
  rawProps: Object
) => {
  setInitialDOMProperties(tag, domElement, rawProps)
}
