export const completeWork = (
  current: Fiber | null,
  workInProgress: Fiber
): Fiber | null => {
  const newProps = workInProgress.pendingProps

  switch (workInProgress.tag) {
    case IndeterminateComponent:
    case FunctionComponent:
      return null
    case HostRoot: {
      //todo
      //   const fiberRoot = workInProgress.stateNode
      bubbleProperties(workInProgress)
      return null
    }
    case HostComponent: {
      const type = workInProgress.type
      if (current !== null && workInProgress.stateNode != null) {
        //todo
        throw new Error('Not Implement')
      } else {
        const instance = createInstance(type, newProps, workInProgress)

        //ç±äºæ¯ååºéåï¼å½workInProgressè¿è¡å½é¶æ®µæ¶ï¼
        //å¶ææå­èç¹é½å·²å®æäºéåå½é¶æ®µï¼ä¹å°±æ¯æå³çå¶å­æ çæædomèç¹å·²ç»åå»º
        //æä»¥åªéè¦æå­æ ä¸­ç¦»instanceæè¿çdomèç¹è¿½å å°instanceä¸å³å¯
        appendAllChildren(instance, workInProgress)
        workInProgress.stateNode = instance

        bubbleProperties(workInProgress)

        if (finalizeInitialChildren(instance, type, newProps)) {
        }
      }

      return null
    }
    case HostText: {
      const newText = newProps

      if (current && workInProgress.stateNode !== null) {
        throw new Error('Not Implement')
      } else {
        workInProgress.stateNode = createTextInstance(newText)
      }
      bubbleProperties(workInProgress)
      return null
    }
  }

  throw new Error('Not implement')
}
