import { createWorkInProgress } from './ReactFiber'
import { beginWork } from './ReactFiberBeginWork'
import {
  commitBeforeMutationEffects,
  commitLayoutEffects,
  commitMutationEffects,
} from './ReactFiberCommitWork'
import { completeWork } from './ReactFiberCompleteWork'
import { MutationMask, NoFlags } from './ReactFiberFlags'
import { Fiber, FiberRoot } from './ReactInternalTypes'
import { HostRoot } from './ReactWorkTags'

/**
 * å½åå¨æå»ºåºç¨çroot
 */
let workInProgressRoot: FiberRoot | null = null

/**
 * å½åæ­£å¨è¿è¡å·¥ä½çfiberèç¹
 */
let workInProgress: Fiber | null = null

const completeUnitOfWork = (unitOfWork: Fiber): void => {
  let completedWork: Fiber | null = unitOfWork

  do {
    const current = completedWork.alternate

    const returnFiber: Fiber | null = completedWork.return

    let next = completeWork(current, completedWork)

    // if (next !== null) {
    //   //// Something suspended. Re-render with the fallback children.
    //   workInProgress = next
    //   return
    // }

    const siblingFiber = completedWork.sibling

    //ç±äºæ¯ååºéåï¼å½ä¸ä¸ªèç¹ç"å½é¶æ®µ"å®æåç«é©¬è¿å¥å¶ä¸ä¸ä¸ªåå¼èç¹çéé¶æ®µ
    if (siblingFiber !== null) {
      workInProgress = siblingFiber
      return
    }

    //returnFiberçææå­èç¹é½å®æéåå½é¶æ®µï¼æ¥ä¸æ¥å°returnFiberçå½é¶æ®µäº
    completedWork = returnFiber
    workInProgress = completedWork
  } while (completedWork !== null)
}

const performUnitOfWork = (unitOfWork: Fiber): void => {
  const current = unitOfWork.alternate

  let next: Fiber | null = null

  //åå»ºæèreconcile unitOfWork.childå¹¶å°å¶è¿å
  next = beginWork(current, unitOfWork)

  //è¿è¡çæ¶ååºéåï¼nextä¸ºnullè¯´æè¯¥èç¹æ²¡æå­èç¹äºï¼å¯¹å¶è¿è¡å½è¿ç¨
  if (next === null) {
    //todo completeUnitofWork
    completeUnitOfWork(unitOfWork)
  } else {
    //å°workInProgressèµå¼ä¸ºunitOfWorkçç¬¬ä¸ä¸ªå­èç¹
    workInProgress = next
  }
}

/**
 *
 * @param root æ°ä¸è½®æ´æ°çFiberRoot
 */
const prepareFreshStack = (root: FiberRoot) => {
  workInProgressRoot = root
  //åå»ºworkInProgressçHostRootå¶propsä¸ºnull
  workInProgress = createWorkInProgress(root.current, null)
}

const renderRootSync = (root: FiberRoot) => {
  //å¦ææ ¹èç¹æ¹åè°ç¨prepareFreshStackéç½®åæ°

  if (workInProgressRoot !== root) {
    prepareFreshStack(root)
  }

  while (workInProgress !== null) {
    performUnitOfWork(workInProgress)
  }
}

const commitRootImpl = (root: FiberRoot): null => {
  const finishedWork = root.finishedWork

  if (finishedWork === null) return null

  root.finishedWork = null

  workInProgressRoot = null
  workInProgress = null

  const subtreeHasEffects =
    (finishedWork.subtreeFlags & MutationMask) !== NoFlags
  const rootHasEffect = (finishedWork.flags & MutationMask) !== NoFlags

  if (rootHasEffect || subtreeHasEffects) {
    commitBeforeMutationEffects(root, finishedWork)

    commitMutationEffects(root, finishedWork)

    root.current = finishedWork

    commitLayoutEffects(finishedWork, root)
  } else {
    root.current = finishedWork
  }

  return null
}

const commitRoot = (root: FiberRoot): null => {
  commitRootImpl(root)
  return null
}

export const performSyncWorkOnRoot = (root: FiberRoot) => {
  const exitStatus = renderRootSync(root)

  const finishedWork: Fiber | null = root.current.alternate

  root.finishedWork = finishedWork

  commitRoot(root)
}

/**
 * è°åº¦fiberèç¹ä¸çæ´æ°
 *
 * @param fiber å½åäº§çæ´æ°çfiberèç¹
 * @returns äº§çæ´æ°fiberæ çFiberRoot(æ³¨æä¸æ¯rootFiber)
 */
export const scheduleUpdateOnFiber = (fiber: Fiber): FiberRoot | null => {
  let node: Fiber = fiber
  let parent: Fiber | null = node.return

  while (parent) {
    //ä¸æ­åä¸éåï¼å½nodeä¸ºHostRootç±»åæ¶ä¼è·³åºå¾ªç¯
    node = parent
    parent = parent.return
  }

  if (node.tag !== HostRoot) {
    return null
  }

  const root = node.stateNode

  performSyncWorkOnRoot(root)

  return root
}

export const discreteUpdates = <A, B, C, D, R>(
  fn: (a: A, b: B, c: C, d: D) => R,
  a: A,
  b: B,
  c: C,
  d: D
): R => {
  return fn(a, b, c, d)
}

export const batchedEventUpdates = <A, R>(fn: (a: A) => R, a: A): R => {
  return fn(a)
}
